import React from "react";
import Link from "next/link";

const grades = [
  { label: "Grade 6", href: "/junior-grades/6" },
  { label: "Grade 7", href: "/junior-grades/7" },
  { label: "Grade 8", href: "/junior-grades/8" },
  { label: "Grade 9", href: "/junior-grades/9" },
  { label: "Grade 10", href: "/junior-grades/10" },
  { label: "A/L ICT", href: "/junior-grades/al-ict" },
];

const GradeNavSection: React.FC = () => {
  return (
    <section className="py-10 bg-[var(--color-light)]">
      <div className="max-w-7xl mx-auto px-6">
        <div className="flex flex-wrap items-center justify-center gap-4">
          {grades.map((item) => (
            <Link
              key={item.label}
              href={item.href}
              className="group relative px-6 py-4 rounded-xl bg-white shadow-card border border-gray-100 transition hover:-translate-y-1 hover:shadow-card-hover"
            >
              <span className="block text-sm font-semibold text-[var(--color-dark)] group-hover:text-[var(--color-primary)] transition">
                {item.label}
              </span>
              <span className="absolute inset-x-0 bottom-0 h-1 bg-[var(--color-primary)] scale-x-0 group-hover:scale-x-100 transition-transform origin-left rounded-b-xl" />
            </Link>
          ))}
        </div>
      </div>
    </section>
  );
};

export default GradeNavSection;
